import classNames from "classnames";
import style from "../assets/scss/index.module.scss";
import React from "react";
import PropTypes from 'prop-types'
import {graphql, Link, useStaticQuery} from "gatsby";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faBookOpen} from '@fortawesome/free-solid-svg-icons'
import {CommentCount} from "disqus-react";

const Article: React.FunctionComponent<any> = (props: any) => {
    const data = useStaticQuery(graphql`
        query {
            file(relativePath: {eq: "myimage.jpg"}) {
                childImageSharp {
                    fluid(maxWidth: 400) {
                        src
                    }
                }
            }
        }
    `)
    return <article className={style.gMb100}>
        <div className={style.gMb30}>
            <span
                className={classNames(style.dBlock, style.gColorGrayDarkV4, style.gFontWeight700, style.gFontSize12, style.textUppercase, style.mb2)}>
                {props.date}
            </span>
            <h2 className={classNames(style.h4, style.gColorBlack, style.gFontWeight600, style.mb3)}>
                <Link className={classNames(style.uLinkV5, style.gColorBlack, style.gColorPrimaryHover)}
                      to={`/posts${props.path}`}>
                    {props.title}
                </Link>
            </h2>
            <p className={classNames(style.gColorGrayDarkV4, style.gLineHeight18)}>
                {props.description}
            </p>
        </div>

        <ul className={classNames(style.listInline, style.gBrdY, style.gBrdGrayLightV3, style.gFontSize13, style.gPy13, style.mb0)}>
            <li className={classNames(style.listInlineItem, style.gColorGrayDarkV4, style.mr2, style.floatRight)}>
                  <span className={classNames(style.dInlineBlock, style.gColorGrayDarkV4)}>
                      <img
                          className={classNames(style.gWidth20, style.gHeight20, style.roundedCircle, style.mr2)}
                          src={data.file.childImageSharp.fluid.src} alt="Bruno Jan"/>
                      Bruno Jan
                  </span>
            </li>
            <li className={classNames(style.listInlineItem, style.gColorGrayDarkV4)}>
                <Link className={classNames(style.dInlineBlock,
                    style.gColorGrayDarkV4,
                    style.gColorWhiteHover,
                    style.gBgGrayDarkV2Hover,
                    style.rounded,
                    style.gTransition03,
                    style.gTextUnderlineNoneHover,
                    style.gPx15,
                    style.gPy5)}
                      to={`/posts${props.path}`}>
                    <FontAwesomeIcon width={15.75}
                                     height={12.25}
                                     className={classNames(style.alignMiddle, style.gFontSizeDefault, style.mr1)}
                                     icon={faBookOpen}/>
                    Read more
                </Link>
            </li>
            <li className={classNames(style.listInlineItem, style.gColorGrayDarkV4)}>
                <CommentCount
                    shortname={"yi-shiuan"}
                    config={
                        {
                            url: `https://yi-shiuan.github.io${props.path}`,
                            identifier: props.id,
                            title: props.title,
                        }
                    }
                >
                    Comments
                </CommentCount>
            </li>
        </ul>
    </article>
}

Article.propTypes = {
    id: PropTypes.string.isRequired,
    title: PropTypes.string.isRequired,
    description: PropTypes.string,
    date: PropTypes.string,
    path: PropTypes.string,
    tags: PropTypes.arrayOf(PropTypes.string)
}

export default Article;